.searchInput {
  border: none;
  outline: 2px solid rgb(8, 77, 102);
  height: 20px;
  color: rgb(8, 77, 102);
  margin-top: 20px;
  padding-left: 10px;
  cursor: pointer;
}

.searchButton {
  border-radius: 0 8px 8px 0;
  padding: 5px 10px;
  background-color: rgb(8, 77, 102);
  color: rgb(218, 229, 233);
  height: 26px;
  cursor: pointer;
}

.searchButton:hover {
  background-color: rgb(2, 30, 40);
}
